.slider {
  position: relative;

  &__item {
    @include flex-s-sb;

    padding: rem(85) rem(48) rem(80) rem(76);

    .text {
      max-width: 240px;
    }
  }

  &__item-inner {
    width: 100%;

    &--content {
      max-width: 460px;
    }

    &--anim {
      max-width: 525px;
    }
  }

  &__item-title {
    margin-bottom: rem(32);
  }

  &__player {
    &--avoid {
      height: 540px;
      transform: translate(-17%, 15%);
    }
  }
}

.slider-alert {
  .slider__item {
    padding: rem(95) rem(50) rem(90) rem(75);
  }

  .text {
    max-width: 355px;
  }

  .progress {
    bottom: rem(93);
  }

  .slider-platform__player {
    height: 355px;
  }
}
